
import React, { Component } from 'react'
import axios from 'axios'; 
import { connect } from 'react-redux';
import '../header/Header'; 
import './Nav.css'; 
import Header from '../header/Header';
import {Link} from "react-router-dom";
import * as actions from '../../Ducks/action_creator';

class Nav extends Component {


    componentDidMount(){
        this.authMe();
    }

    authMe = () => {
        // debugger; 
        axios.get('/api/auth/me').then(res => {
            // debugger; 
            if(res.data){
                this.props.setUser(res.data.user);
                this.props.setWeight(res.data.weight);
                this.props.setMacros(res.data.macros); 
            }else{
                this.props.history.push('/')
            }
        })
        .catch(err => console.log(err))
   }

   

    render() {
        const prof_pic = {
            yourFace : `url(${this.props.user.prof_pic})`
        }
        return (
            <div className='navPage'>
                <Header/>
                <div className='navBar'>
                    <div>
                        <Link to='/home' className='linksHome'>
                            <img className="navieButton" src={navieHome} alt=""/> 
                        </Link>
                    </div>
                    <div>
                        <Link to='/goals' className='links'>
                            Goals
                        </Link>
                    </div>
                    <div>
                        <Link to='/recipeForm' className='links'>
                            Make a Meal
                        </Link>
                    </div>
                    <div>
                        <Link to='/lunchbox' className='links'>
                            Lunch Box
                        </Link>
                    </div>
                    <div>
                        <Link to='/planning' className='links'>
                            Meal Plan
                        </Link>
                    </div> 

                    <div className='prof_pic' style={{backgroundImage: prof_pic.yourFace}}/>
                    <div className='listingUser' onClick={this.authMe}>{this.props.user.first_name} {this.props.user.last_name}</div>
               </div>
            </div>
        )
    }
}

export default connect(state => state, actions)(Nav);



let navieHome = "data:image/png;base64,"; 
